import React from 'react'
import Link from 'gatsby-link'

const NotFoundPage = () => (
  <div>
    <h1>NOT FOUND</h1>
    <p>
      Now you've done it; you've destroyed the known Internet with your frenetic
      typing.
    </p>
    <p>
      Press the <strong>back</strong> button or perhaps{' '}
      <Link to="/">go back to the homepage</Link>.
    </p>
  </div>
)

export default NotFoundPage



